import React from 'react'
import { useAuth } from '../contexts/AuthContext'

// 
import Login from '../pages/Login'
import Chat from '../pages/Chat'
import Dashboard from '../pages/Dashboard'
import UserManagement from '../pages/admin/UserManagement'
import RoleManagement from '../pages/admin/RoleManagement'
import PermissionManagement from '../pages/admin/PermissionManagement'
import MenuManagement from '../pages/admin/MenuManagement'
import TeamManagement from '../pages/admin/TeamManagement'
import ProjectManagement from '../pages/admin/ProjectManagement'
import DocumentManagement from '../pages/admin/DocumentManagement'
import Notebook from '../pages/Notebook'
import FileResourceManagement from '../pages/admin/FileResourceManagement'
import TenantManagement from '../pages/admin/TenantManagement'
import AISupportManagement from '../pages/admin/AISupportManagement'
import GitCredentialManagement from '../pages/admin/GitCredentialManagement'
import ComponentsPage from '../pages/ComponentsPage'
import NotFound from '../pages/NotFound'
import { LoadingSpinner } from '../components'

export type Route = 
  | 'home'
  | 'login'
  | 'dashboard'
  | 'users'
  | 'roles'
  | 'permissions'
  | 'menus'
  | 'teams'
  | 'projects'
  | 'documents'
  | 'notebook'
  | 'file-resources'
  | 'tenants'
  | 'ai-supports'
  | 'git-credentials'
  | 'components'

interface RouterContextType {
  route: Route
  navigate: (route: Route) => void
}

const RouterContext = React.createContext<RouterContextType | undefined>(undefined)

export function RouterProvider({ children }: { children: React.ReactNode }) {
  const [route, setRoute] = React.useState<Route>(() => {
    const saved = localStorage.getItem('current_route')
    return (saved as Route) || 'home'
  })
  
  const navigate = React.useCallback((newRoute: Route) => {
    setRoute(newRoute)
    localStorage.setItem('current_route', newRoute)
  }, [])

  return (
    <RouterContext.Provider value={{ route, navigate }}>
      {children}
    </RouterContext.Provider>
  )
}

export function useRouter() {
  const context = React.useContext(RouterContext)
  if (context === undefined) {
    throw new Error('useRouter must be used within a RouterProvider')
  }
  return context
}

// 
function ProtectedRoute({ children }: { children: React.ReactNode }) {
  const { isAuthenticated, isLoading } = useAuth()
  const { navigate } = useRouter()

  React.useEffect(() => {
    if (!isLoading && !isAuthenticated) {
      navigate('login')
    }
  }, [isAuthenticated, isLoading, navigate])

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-surface-50 dark:bg-surface-950">
        <LoadingSpinner size="lg" text="..." />
      </div>
    )
  }

  if (!isAuthenticated) {
    return null
  }

  return <>{children}</>
}

//  - 
function PublicRoute({ children, redirectTo = 'home' }: { children: React.ReactNode; redirectTo?: Route }) {
  const { isAuthenticated, isLoading } = useAuth()
  const { navigate } = useRouter()

  React.useEffect(() => {
    if (!isLoading && isAuthenticated) {
      navigate(redirectTo)
    }
  }, [isAuthenticated, isLoading, navigate, redirectTo])

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-surface-50 dark:bg-surface-950">
        <LoadingSpinner size="lg" text="..." />
      </div>
    )
  }

  if (isAuthenticated) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-surface-50 dark:bg-surface-950">
        <LoadingSpinner size="lg" text="..." />
      </div>
    )
  }

  return <>{children}</>
}

// 
export function RouteRenderer() {
  const { route, navigate } = useRouter()

  switch (route) {
    case 'home':
      return (
        <ProtectedRoute>
          <Chat />
        </ProtectedRoute>
      )
    case 'login':
      return (
        <PublicRoute>
          <Login 
            onBack={() => navigate('home')} 
            onLogin={() => navigate('home')} 
          />
        </PublicRoute>
      )
    case 'dashboard':
      return (
        <ProtectedRoute>
          <Dashboard />
        </ProtectedRoute>
      )
    case 'users':
      return (
        <ProtectedRoute>
          <UserManagement />
        </ProtectedRoute>
      )
    case 'roles':
      return (
        <ProtectedRoute>
          <RoleManagement />
        </ProtectedRoute>
      )
    case 'permissions':
      return (
        <ProtectedRoute>
          <PermissionManagement />
        </ProtectedRoute>
      )
    case 'menus':
      return (
        <ProtectedRoute>
          <MenuManagement />
        </ProtectedRoute>
      )
    case 'teams':
      return (
        <ProtectedRoute>
          <TeamManagement />
        </ProtectedRoute>
      )
    case 'projects':
      return (
        <ProtectedRoute>
          <ProjectManagement />
        </ProtectedRoute>
      )
    case 'documents':
      return (
        <ProtectedRoute>
          <DocumentManagement />
        </ProtectedRoute>
      )
    case 'file-resources':
      return (
        <ProtectedRoute>
          <FileResourceManagement />
        </ProtectedRoute>
      )
    case 'tenants':
      return (
        <ProtectedRoute>
          <TenantManagement />
        </ProtectedRoute>
      )
    case 'ai-supports':
      return (
        <ProtectedRoute>
          <AISupportManagement />
        </ProtectedRoute>
      )
    case 'git-credentials':
      return (
        <ProtectedRoute>
          <GitCredentialManagement />
        </ProtectedRoute>
      )
    case 'components':
      return <ComponentsPage />
    case 'notebook':
      return (
        <ProtectedRoute>
          <Notebook />
        </ProtectedRoute>
      )
    default:
      return <NotFound />
  }
}
