import React, { useState, useEffect } from 'react'
import { Button, Card, Input, Modal, Table, Badge, EmptyState, LoadingSpinner } from '../../components'
import { menusApi } from '../../services/auth'
import { useToast } from '../../components/ToastProvider'
import { 
  PlusIcon, 
  PencilIcon, 
  TrashIcon, 
  MagnifyingGlassIcon,
  ListBulletIcon,
  ArrowPathIcon,
  LinkIcon,
  FolderIcon
} from '@heroicons/react/24/solid'

interface Menu {
  id: string
  name: string
  code: string
  description?: string
  parentId?: string
  url?: string
  icon?: string
  sort: number
}

export default function MenuManagement() {
  const [menus, setMenus] = useState<Menu[]>([])
  const [filteredMenus, setFilteredMenus] = useState<Menu[]>([])
  const [loading, setLoading] = useState(false)
  const [searchQuery, setSearchQuery] = useState('')
  const [showModal, setShowModal] = useState(false)
  const [showDeleteModal, setShowDeleteModal] = useState(false)
  const [editingMenu, setEditingMenu] = useState<Menu | null>(null)
  const [deletingMenu, setDeletingMenu] = useState<Menu | null>(null)
  const [formData, setFormData] = useState({
    name: '',
    code: '',
    description: '',
    parentId: '',
    url: '',
    icon: '',
    sort: 0,
  })
  const [formErrors, setFormErrors] = useState<Record<string, string>>({})
  const { show: showToast } = useToast()

  const fetchMenus = async () => {
    setLoading(true)
    try {
      const response = await menusApi.apiMenuListGet()
      const data = (response.data.data as Menu[]) || []
      setMenus(data)
      setFilteredMenus(data)
    } catch (error) {
      showToast('', 'error')
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchMenus()
  }, [])

  useEffect(() => {
    if (!searchQuery.trim()) {
      setFilteredMenus(menus)
      return
    }
    const query = searchQuery.toLowerCase()
    const filtered = menus.filter(menu => 
      menu.name.toLowerCase().includes(query) ||
      menu.code.toLowerCase().includes(query) ||
      menu.description?.toLowerCase().includes(query)
    )
    setFilteredMenus(filtered)
  }, [searchQuery, menus])

  const validateForm = () => {
    const errors: Record<string, string> = {}
    if (!formData.name.trim()) {
      errors.name = ''
    }
    if (!formData.code.trim()) {
      errors.code = ''
    }
    setFormErrors(errors)
    return Object.keys(errors).length === 0
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!validateForm()) return

    try {
      if (editingMenu) {
        await menusApi.apiMenuUpsertPost({
          id: editingMenu.id,
          ...formData,
        })
        showToast('', 'success')
      } else {
        await menusApi.apiMenuUpsertPost(formData)
        showToast('', 'success')
      }
      closeModal()
      fetchMenus()
    } catch (error) {
      showToast('', 'error')
    }
  }

  const handleEdit = (menu: Menu) => {
    setEditingMenu(menu)
    setFormData({
      name: menu.name,
      code: menu.code,
      description: menu.description || '',
      parentId: menu.parentId || '',
      url: menu.url || '',
      icon: menu.icon || '',
      sort: menu.sort,
    })
    setFormErrors({})
    setShowModal(true)
  }

  const handleDelete = (menu: Menu) => {
    setDeletingMenu(menu)
    setShowDeleteModal(true)
  }

  const confirmDelete = async () => {
    if (!deletingMenu) return
    try {
      await menusApi.apiMenuDeleteDelete({ ids: [deletingMenu.id] })
      showToast('', 'success')
      fetchMenus()
    } catch (error) {
      showToast('', 'error')
    } finally {
      setShowDeleteModal(false)
      setDeletingMenu(null)
    }
  }

  const closeModal = () => {
    setShowModal(false)
    setEditingMenu(null)
    setFormData({ name: '', code: '', description: '', parentId: '', url: '', icon: '', sort: 0 })
    setFormErrors({})
  }

  const openCreateModal = () => {
    setEditingMenu(null)
    setFormData({ name: '', code: '', description: '', parentId: '', url: '', icon: '', sort: 0 })
    setFormErrors({})
    setShowModal(true)
  }

  const getParentName = (parentId?: string) => {
    if (!parentId) return '-'
    return menus.find(m => m.id === parentId)?.name || ''
  }

  const columns = [
    {
      key: 'name',
      title: '',
      render: (menu: Menu) => (
        <div className="flex items-center gap-3">
          <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-cyan-500 to-blue-500 flex items-center justify-center text-white">
            <ListBulletIcon className="w-5 h-5" />
          </div>
          <div>
            <div className="font-medium text-surface-900 dark:text-white">{menu.name}</div>
            <div className="text-xs text-surface-500">{menu.code}</div>
          </div>
        </div>
      )
    },
    {
      key: 'url',
      title: '',
      render: (menu: Menu) => (
        <div className="flex items-center gap-2">
          <LinkIcon className="w-4 h-4 text-surface-400" />
          <span className="text-sm text-surface-600 dark:text-surface-400 truncate max-w-xs">
            {menu.url || '-'}
          </span>
        </div>
      )
    },
    {
      key: 'parent',
      title: '',
      render: (menu: Menu) => (
        <div className="flex items-center gap-2">
          <FolderIcon className="w-4 h-4 text-surface-400" />
          <span className="text-sm text-surface-600 dark:text-surface-400">
            {getParentName(menu.parentId)}
          </span>
        </div>
      )
    },
    {
      key: 'sort',
      title: '',
      render: (menu: Menu) => (
        <Badge variant="default" size="sm">{menu.sort}</Badge>
      )
    },
    {
      key: 'actions',
      title: '',
      align: 'right' as const,
      render: (menu: Menu) => (
        <div className="flex items-center justify-end gap-2">
          <button
            onClick={() => handleEdit(menu)}
            className="p-2 rounded-lg text-surface-600 hover:text-primary-600 hover:bg-primary-50 dark:hover:bg-primary-900/20 transition-colors"
            title=""
          >
            <PencilIcon className="w-4 h-4" />
          </button>
          <button
            onClick={() => handleDelete(menu)}
            className="p-2 rounded-lg text-surface-600 hover:text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20 transition-colors"
            title=""
          >
            <TrashIcon className="w-4 h-4" />
          </button>
        </div>
      )
    },
  ]

  return (
    <div className="animate-fade-in">
      {/* Header */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4 mb-6">
        <div>
          <h1 className="text-2xl font-bold text-surface-900 dark:text-white"></h1>
          <p className="text-sm text-surface-500 dark:text-surface-400 mt-1">
            
          </p>
        </div>
        <Button onClick={openCreateModal} leftIcon={<PlusIcon className="w-4 h-4" />}>
          
        </Button>
      </div>

      {/* Filters */}
      <Card className="mb-6">
        <div className="flex flex-col sm:flex-row gap-4">
          <div className="flex-1 relative">
            <MagnifyingGlassIcon className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-surface-400" />
            <input
              type="text"
              placeholder="..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="w-full pl-10 pr-4 py-2.5 rounded-xl border border-surface-300 dark:border-surface-600 bg-white dark:bg-surface-900 text-surface-900 dark:text-surface-100 focus:outline-none focus:ring-2 focus:ring-primary-500/20 focus:border-primary-500"
            />
          </div>
          <Button 
            variant="secondary" 
            leftIcon={<ArrowPathIcon className="w-4 h-4" />}
            onClick={fetchMenus}
          >
            
          </Button>
        </div>
      </Card>

      {/* Table */}
      <Card>
        {loading ? (
          <LoadingSpinner centered text="..." />
        ) : filteredMenus.length === 0 ? (
          <EmptyState
            title={searchQuery ? '' : ''}
            description={searchQuery ? '' : ''}
            action={!searchQuery ? { label: '', onClick: openCreateModal } : undefined}
          />
        ) : (
          <Table
            columns={columns}
            dataSource={filteredMenus}
            rowKey="id"
          />
        )}
      </Card>

      {/* Create/Edit Modal */}
      <Modal
        isOpen={showModal}
        onClose={closeModal}
        title={editingMenu ? '' : ''}
        description={editingMenu ? '' : ''}
        size="lg"
        footer={
          <div className="flex justify-end gap-3">
            <Button variant="secondary" onClick={closeModal}>
              
            </Button>
            <Button onClick={handleSubmit}>
              {editingMenu ? '' : ''}
            </Button>
          </div>
        }
      >
        <form className="space-y-5">
          <div className="grid sm:grid-cols-2 gap-4">
            <Input
              label=""
              placeholder=""
              value={formData.name}
              onChange={(e) => setFormData({ ...formData, name: e.target.value })}
              error={formErrors.name}
              required
            />
            <Input
              label=""
              placeholder=": user_management"
              value={formData.code}
              onChange={(e) => setFormData({ ...formData, code: e.target.value })}
              error={formErrors.code}
              required
            />
          </div>

          <div className="grid sm:grid-cols-2 gap-4">
            <div>
              <label className="form-label"></label>
              <select
                value={formData.parentId}
                onChange={(e) => setFormData({ ...formData, parentId: e.target.value })}
                className="w-full px-4 py-3 rounded-xl border border-surface-300 dark:border-surface-600 bg-white dark:bg-surface-900 text-surface-900 dark:text-surface-100 focus:outline-none focus:ring-2 focus:ring-primary-500/20 focus:border-primary-500"
              >
                <option value="">--  --</option>
                {menus
                  .filter(m => m.id !== editingMenu?.id)
                  .map(menu => (
                    <option key={menu.id} value={menu.id}>{menu.name}</option>
                  ))}
              </select>
            </div>
            <Input
              label=""
              type="number"
              placeholder="0"
              value={formData.sort.toString()}
              onChange={(e) => setFormData({ ...formData, sort: parseInt(e.target.value) || 0 })}
            />
          </div>

          <Input
            label="URL "
            placeholder=": /users"
            value={formData.url}
            onChange={(e) => setFormData({ ...formData, url: e.target.value })}
          />

          <Input
            label=""
            placeholder=": UsersIcon"
            value={formData.icon}
            onChange={(e) => setFormData({ ...formData, icon: e.target.value })}
          />

          <Input
            label=""
            placeholder=""
            value={formData.description}
            onChange={(e) => setFormData({ ...formData, description: e.target.value })}
          />
        </form>
      </Modal>

      {/* Delete Confirmation Modal */}
      <Modal
        isOpen={showDeleteModal}
        onClose={() => setShowDeleteModal(false)}
        title=""
        description={` "${deletingMenu?.name}" `}
        size="sm"
        footer={
          <div className="flex justify-end gap-3">
            <Button variant="secondary" onClick={() => setShowDeleteModal(false)}>
              
            </Button>
            <Button variant="danger" onClick={confirmDelete}>
              
            </Button>
          </div>
        }
      />
    </div>
  )
}
