import React, { useState } from 'react'
import { 
  Button, 
  Card, 
  Input, 
  TextArea, 
  Select,
  Badge, 
  Avatar,
  Modal,
  Table,
  Skeleton,
  EmptyState,
  LoadingSpinner,
  StatCard
} from '../components'
import { 
  BellIcon, 
  CheckCircleIcon, 
  ExclamationTriangleIcon,
  UserIcon,
  PlusIcon
} from '@heroicons/react/24/solid'

export default function ComponentsPage() {
  const [showModal, setShowModal] = useState(false)
  const [inputValue, setInputValue] = useState('')
  const [selectValue, setSelectValue] = useState('')

  const sampleData = [
    { id: '1', name: '', email: 'zhangsan@example.com', role: '', status: 'active' },
    { id: '2', name: '', email: 'lisi@example.com', role: '', status: 'active' },
    { id: '3', name: '', email: 'wangwu@example.com', role: '', status: 'inactive' },
  ]

  const columns = [
    { key: 'name', title: '', dataIndex: 'name' as const },
    { key: 'email', title: '', dataIndex: 'email' as const },
    { key: 'role', title: '', dataIndex: 'role' as const },
    { 
      key: 'status', 
      title: '',
      render: (record: any) => (
        <Badge variant={record.status === 'active' ? 'success' : 'default'} size="sm">
          {record.status === 'active' ? '' : ''}
        </Badge>
      )
    },
  ]

  return (
    <div className="container-main py-8 animate-fade-in">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-surface-900 dark:text-white mb-2">
          
        </h1>
        <p className="text-surface-500 dark:text-surface-400">
           Neuro UI 
        </p>
      </div>

      <div className="space-y-8">
        {/* Buttons */}
        <Card>
          <h2 className="text-xl font-bold text-surface-900 dark:text-white mb-6"> Button</h2>
          <div className="space-y-4">
            <div className="flex flex-wrap items-center gap-3">
              <Button variant="primary"></Button>
              <Button variant="secondary"></Button>
              <Button variant="ghost"></Button>
              <Button variant="danger"></Button>
            </div>
            <div className="flex flex-wrap items-center gap-3">
              <Button size="sm"></Button>
              <Button size="md"></Button>
              <Button size="lg"></Button>
            </div>
            <div className="flex flex-wrap items-center gap-3">
              <Button isLoading></Button>
              <Button disabled></Button>
              <Button leftIcon={<PlusIcon className="w-4 h-4" />}></Button>
            </div>
          </div>
        </Card>

        {/* Form Controls */}
        <Card>
          <h2 className="text-xl font-bold text-surface-900 dark:text-white mb-6"> Form</h2>
          <div className="grid md:grid-cols-2 gap-6">
            <div className="space-y-4">
              <Input 
                label=""
                placeholder=""
                value={inputValue}
                onChange={(e) => setInputValue(e.target.value)}
              />
              <Input 
                label=""
                placeholder="..."
                leftIcon={<BellIcon className="w-5 h-5" />}
              />
              <Input 
                label=""
                placeholder=""
                error=""
              />
              <Input 
                label=""
                placeholder=""
                disabled
              />
            </div>
            <div className="space-y-4">
              <TextArea 
                label=""
                placeholder="..."
                rows={4}
              />
              <Select
                label=""
                value={selectValue}
                onChange={(e) => setSelectValue(e.target.value)}
                options={[
                  { value: '', label: '' },
                  { value: '1', label: '' },
                  { value: '2', label: '' },
                  { value: '3', label: '' },
                ]}
              />
            </div>
          </div>
        </Card>

        {/* Badges */}
        <Card>
          <h2 className="text-xl font-bold text-surface-900 dark:text-white mb-6"> Badge</h2>
          <div className="flex flex-wrap items-center gap-3">
            <Badge></Badge>
            <Badge variant="primary"></Badge>
            <Badge variant="success"></Badge>
            <Badge variant="warning"></Badge>
            <Badge variant="danger"></Badge>
            <Badge variant="info"></Badge>
          </div>
          <div className="flex flex-wrap items-center gap-3 mt-4">
            <Badge size="sm"></Badge>
            <Badge size="md"></Badge>
            <Badge size="lg"></Badge>
          </div>
          <div className="flex flex-wrap items-center gap-3 mt-4">
            <Badge dot></Badge>
            <Badge dot pulse></Badge>
          </div>
        </Card>

        {/* Avatars */}
        <Card>
          <h2 className="text-xl font-bold text-surface-900 dark:text-white mb-6"> Avatar</h2>
          <div className="flex flex-wrap items-center gap-4">
            <Avatar size="xs" name="" />
            <Avatar size="sm" name="" />
            <Avatar size="md" name="" />
            <Avatar size="lg" name="" />
            <Avatar size="xl" name="" />
          </div>
          <div className="flex flex-wrap items-center gap-4 mt-4">
            <Avatar src="https://i.pravatar.cc/150?img=1" name="User 1" />
            <Avatar src="https://i.pravatar.cc/150?img=2" name="User 2" />
            <Avatar src="https://i.pravatar.cc/150?img=3" name="User 3" />
            <Avatar src="/broken-url.jpg" name="Fallback" />
          </div>
        </Card>

        {/* Stat Cards */}
        <Card>
          <h2 className="text-xl font-bold text-surface-900 dark:text-white mb-6"> StatCard</h2>
          <div className="grid sm:grid-cols-2 lg:grid-cols-4 gap-4">
            <StatCard 
              title="" 
              value="12,345" 
              change="+12%" 
              changeType="positive"
              icon={<UserIcon className="w-6 h-6" />}
            />
            <StatCard 
              title="" 
              value="8,234" 
              change="+5%" 
              changeType="positive"
              icon={<CheckCircleIcon className="w-6 h-6" />}
            />
            <StatCard 
              title="" 
              value="23" 
              change="-3" 
              changeType="negative"
              icon={<ExclamationTriangleIcon className="w-6 h-6" />}
            />
            <StatCard 
              title="" 
              value="" 
              changeType="neutral"
              icon={<BellIcon className="w-6 h-6" />}
            />
          </div>
        </Card>

        {/* Table */}
        <Card>
          <h2 className="text-xl font-bold text-surface-900 dark:text-white mb-6"> Table</h2>
          <Table 
            columns={columns}
            dataSource={sampleData}
            rowKey="id"
          />
        </Card>

        {/* Modal */}
        <Card>
          <h2 className="text-xl font-bold text-surface-900 dark:text-white mb-6"> Modal</h2>
          <div className="flex flex-wrap gap-3">
            <Button onClick={() => setShowModal(true)}></Button>
          </div>
        </Card>

        {/* Loading States */}
        <Card>
          <h2 className="text-xl font-bold text-surface-900 dark:text-white mb-6"> Loading</h2>
          <div className="flex flex-wrap items-center gap-6">
            <LoadingSpinner size="sm" />
            <LoadingSpinner size="md" />
            <LoadingSpinner size="lg" />
            <LoadingSpinner size="xl" />
          </div>
          <div className="mt-6 p-8 bg-surface-50 dark:bg-surface-900 rounded-xl">
            <LoadingSpinner centered text="..." />
          </div>
        </Card>

        {/* Skeleton */}
        <Card>
          <h2 className="text-xl font-bold text-surface-900 dark:text-white mb-6"> Skeleton</h2>
          <div className="space-y-4">
            <div className="flex items-center gap-4">
              <Skeleton circle width={48} height={48} />
              <div className="flex-1 space-y-2">
                <Skeleton width="60%" height={20} />
                <Skeleton width="40%" height={16} />
              </div>
            </div>
            <Skeleton count={3} height={16} />
          </div>
        </Card>

        {/* Empty State */}
        <Card>
          <h2 className="text-xl font-bold text-surface-900 dark:text-white mb-6"> EmptyState</h2>
          <EmptyState 
            title=""
            description=""
            action={{ label: '', onClick: () => alert('') }}
          />
        </Card>
      </div>

      {/* Demo Modal */}
      <Modal
        isOpen={showModal}
        onClose={() => setShowModal(false)}
        title=""
        description=""
        footer={
          <div className="flex justify-end gap-3">
            <Button variant="secondary" onClick={() => setShowModal(false)}>
              
            </Button>
            <Button onClick={() => setShowModal(false)}>
              
            </Button>
          </div>
        }
      >
        <p className="text-surface-600 dark:text-surface-400">
          
        </p>
      </Modal>
    </div>
  )
}
